<template>
  <meta name="viewport" content="width=device-width, initial-scale=1.0" />
  <div ref="playCanvasContainer" class="playcanvas-container"></div>
</template>

<script>
import * as pc from "playcanvas";

export default {
  name: "ModelBackground",
  mounted() {
    //Create a PlayCanvas application
    const canvas = document.createElement("canvas");
    canvas.classList.add("playcanvas-canvas");
    this.$refs.playCanvasContainer.appendChild(canvas);
    const app = new pc.Application(canvas, {});

    //Load the GLTF 3D model
    app.assets.loadFromUrl(
      "../public/gengar.obj",
      "container",
      (err, asset) => {
        if (!err) {
          const entity = new pc.Entity();
          entity.addComponent("model", {
            type: "asset",
            asset: asset.resource.model,
          });
          app.root.addChild(entity);
        } else {
          console.error("Failed to load GLTF model:", err);
        }
      }
    );

    //Set up rotation animation
    app.on("update", () => {
      const entity = app.root.findByName("public");
      if (entity) {
        entity.rotate(0, 0.01, 0);
      }
    });

    //Start the application
    app.start();
  },
};
</script>

<style scoped lang="scss">
.playcanvas-container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.playcanvas-canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
</style>
